@mixin flex-container($justify-content, $direction) {
  display: flex;
  align-items: center;
  justify-content: $justify-content;
  flex-direction: $direction; }


.card {
  //width: 100%
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  //margin: 7px
  background-color: white;
  border-radius: 10px; }

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2); }

.bg-color {
  background-color: white; }

.myItem {
  //padding: 0
  word-wrap: break-word;
  white-space: pre-wrap; }

.button {
  @include flex-container(start, row);
  margin-top: 30px;
  padding: 0; }


.list {
  @include flex-container(center, column);
  //margin: 0 0 0 0
  //padding: 0
  word-wrap: break-word; }

.Cell__main {
  white-space: pre-wrap; }



